  width: 50rem;
  height: 4.2rem;
  display: -webkit-flex;
  display: flex;
  /* grid-template-columns: repeat(auto-fit, minmax(2px, auto)); */
  -webkit-align-items: start;
          align-items: start;
}

.hoverable a {
  text-decoration: none;
}

.hoverable a:hover {
  text-decoration: none;
  color: #2B3252 !important;
  color: var(--new-work-studio-log-color) !important;
}

.logo a:first-child {
