  font-size: 20px;
  width: auto;
  padding-left: 30px;
  padding-right: 5px;
  color: #2B3252 !important;
  color: var(--new-work-studio-log-color) !important;
}

.logo a:first-child:hover {
  text-decoration: none;
  color: #2B3252 !important;
  color: var(--new-work-studio-log-color) !important;
}

.owner-name-display {
  font-family: 'Cookie', cursive;
  color: #FAD744;
  font-size: 40px;
}

.toastClass {
  background-color: #EF5455;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: 16px;
}

.templateSection {
  min-height: 10rem;
  max-height: 10rem;
  overflow-y: auto;
}

.templateSection::-webkit-scrollbar-track
 {
     -webkit-box-shadow: inset 0 0 6px #F5F5F5;
